
import {defineComponent} from 'vue';
import {
	CategoryScale,
	Chart,
	Legend,
	LinearScale,
	LineController,
	LineElement,
	PointElement,
	Title,
	Tooltip
} from "chart.js";
import {LineChart} from "vue-chart-3";

Chart.register(LineController, CategoryScale, LinearScale, PointElement, LineElement, Legend, Title, Tooltip);

const states = ["Alabama", "Alaska", "Arizona", "Arkansas", "California", "Colorado", "Connecticut", "Delaware", "Florida", "Georgia", "Hawaii", "Idaho", "Illinois", "Indiana", "Iowa", "Kansas", "Kentucky", "Louisiana", "Maine", "Maryland", "Massachusetts", "Michigan", "Minnesota", "Mississippi", "Missouri", "Montana", "Nebraska", "Nevada", "New Hampshire", "New Jersey", "New Mexico", "New York", "North Carolina", "North Dakota", "Ohio", "Oklahoma", "Oregon", "Pennsylvania", "Rhode Island", "South Carolina", "South Dakota", "Tennessee", "Texas", "Utah", "Vermont", "Virginia", "Washington", "West Virginia", "Wisconsin", "Wyoming"];
const timeLimit = 600;

export default defineComponent({
	name: 'App',
	components: {LineChart},
	data: () => ({
		states,
		guess: '',
		found: [] as string[],
		foundAt: [] as number[],
		available: [] as string[],
		message: '',
		gameStarted: false,
		gameOver: false,
		gameWon: false,
		timer: -1,
		timerHandle: -1,
		showLiveChart: false,
		chartOptions: {
			responsive: true,
			interaction: {
				mode: 'index',
				intersect: false,
			},
			stacked: false,
			plugins: {
				legend: {
					position: 'top',
				},
				title: {
					display: true,
					text: 'States guessed per second',
				},
			},
			scales: {
				y: {
					type: 'linear',
					display: true,
					position: 'left',
				},
				y1: {
					type: 'linear',
					display: true,
					position: 'right',
					grid: {
						drawOnChartArea: false, // only want the grid lines for one axis to show up
					},
				},
			}
		}
	}),

	methods: {
		startGame() {
			this.found = [];
			this.foundAt = [];
			this.guess = '';
			this.message = '';
			this.gameWon = false;
			this.gameOver = false;
			this.gameStarted = true;
			this.timer = timeLimit;
			this.available = [...states];
			this.timerHandle = setInterval(() => {
				if (this.timer > 0) {
					this.timer--;
				} else {
					this.endGame();

					clearInterval(this.timerHandle);
				}
			}, 1000);
			window.onbeforeunload = (e) => {
				e.preventDefault();
				e.returnValue = "Your progress will not be saved. Are you sure you want to leave?";
			};
		},

		endGame() {
			window.onbeforeunload = null;
			clearInterval(this.timerHandle);
			this.gameOver = true;
			this.gameWon = this.found.length === states.length;
		},
	},

	watch: {
		guess() {
			let normalizedGuess = this.guess.toLowerCase().trim();
			let idx = this.available.map(state => state.toLowerCase()).findIndex(state => state === normalizedGuess);
			if (idx >= 0) {
				let matched = this.available.splice(idx, 1);
				this.found.push(matched[0]);
				this.found.sort();
				this.foundAt.push(this.timer);
				this.guess = '';

				if (this.available.length === 0) {
					this.endGame();
				}
			}
		}
	},

	computed: {
		foundChartData() {
			let points = [];
			let statesPerSecond = 0;
			let averageStatesPerSecond = 0;
			let lastTimestamp = 0;
			let stateCount = 1;
			let averageWeight = 0.9;
			for (let timestamp of this.foundAt.map(timestamp => timeLimit - timestamp)) {
				if (lastTimestamp > 0) {
					statesPerSecond = (stateCount - 1) / timestamp;
					averageStatesPerSecond = averageWeight * averageStatesPerSecond + (1 - averageWeight) * statesPerSecond;
				}

				points.push({timestamp, statesPerSecond, averageStatesPerSecond, stateCount});
				stateCount++;
				lastTimestamp = timestamp;
			}

			// throw away first measurement since it is inaccurate
			points.shift();

			return {
				labels: [...Array(timeLimit - this.timer).keys()].map(i => i + 1).slice(1),
				datasets: [
					{
						label: 'Average States Per Second',
						data: points.map(point => {
							return {x: point.timestamp, y: point.averageStatesPerSecond};
						}),
						borderColor: '#3c8',
						tension: 0.2,
					},
					{
						label: 'Raw States Per Second',
						data: points.map(point => { return {x: point.timestamp, y: point.statesPerSecond}; }),
						borderColor: '#aaaaaa99',
						tension: 0.1,
					},
					{
						label: 'Total States Found',
						data: points.map(point => { return {x: point.timestamp, y: point.stateCount}; }),
						borderColor: '#dd9999aa',
						tension: 0.1,
						yAxisID: 'y1',
					},
				]
			}
		},

		tweetLink() {
			let timeTaken = Math.ceil((timeLimit - this.timer) / 60);
			let timeTakenUnit = timeTaken === 1 ? 'minute' : 'minutes';
			let statesFound = this.found.length;
			let totalStates = states.length;
			let text = 'I guessed ';
			if (this.found.length < states.length) {
				text += statesFound + ' out of ';
			} else {
				text += 'all ';
			}
			text += totalStates + ' US states within ' + timeTaken + ' ' + timeTakenUnit + '!\n\nTry it yourself: https://states-game.ricardoboss.de';

			return 'https://twitter.com/intent/tweet?text=' + encodeURIComponent(text);
		}
	}
});
